<template>
  <div class="home">
    <el-backtop/>
    <el-container>
      <el-header class="homeHeader">
        <div class="title">
          {{ systemInfo.name }} {{ systemInfo.version }}
        </div>

        <div class="textArr">
          <p class="slice-enter-active" :style="{color:text.color}" :key="text.id">
            {{ text.value }}</p>
        </div>

        <div>
          <!--占位-->
        </div>

      </el-header>

      <el-container>

        <el-aside width="168">
          <div align="center">
            <el-radio-group v-model="isCollapse" style="margin-top: 20px;margin-bottom: 10px;width: 100%">
              <el-radio-button :label="!isCollapse" title="展示菜单">
                <i :class="isCollapse?'el-icon-d-arrow-right':'el-icon-d-arrow-left'"></i></el-radio-button>
            </el-radio-group>
          </div>
          <el-menu
              :collapse="isCollapse"
              :unique-opened="true"
              :default-active="menuDefaultActive"
              class="el-menu-vertical-demo">

            <el-submenu v-for="(menu1,index1) in menus" :key="index1" :index="menu1.index"
                        :disabled="menu1.disabled">
              <template #title>
                <el-icon :class="menu1.icon"></el-icon>
                <span>{{ menu1.name }}</span>
              </template>
              <el-menu-item v-for="(menu2,index2) in menu1.children" :key="index2"
                            :index="menu2.index" @click="goPath(menu2.url)"
                            :disabled="menu2.disabled">
                <el-icon :class="menu2.icon"></el-icon>
                {{ menu2.name }}
              </el-menu-item>
            </el-submenu>
          </el-menu>

        </el-aside>
        <el-main>
          <router-view/>
        </el-main>
      </el-container>
    </el-container>

  </div>
</template>

<script>

export default {
  name: 'HomeView',
  data: () => ({
    isCollapse: true,//菜单栏收缩开关
    menus: {},//菜单列表
    menuDefaultActive: null,//默认展示页面
    systemInfo: {},
    scroll: { //滚动公告
      number: 0,
      textArr: [{
        "value": "",
        "color": ""
      }]
    }
  }),
  computed: {
    text() {
      return {
        id: this.scroll.number,
        value: this.scroll.textArr[this.scroll.number].value,
        color: this.scroll.textArr[this.scroll.number].color,
      }
    }
  },
  mounted() {
    this.initVersionInfo();
    this.initMenus();
    this.initScrollTextArr();
    //滚动公告
    this.startMove();
  },
  methods: {

    /**
     * 初始化公告列表
     */
    initScrollTextArr() {
      this.scroll.textArr.push({
        value: '云上游乐园 2.0上线',
        color: '#' + (Math.random() * 0xffffff << 0).toString(16)
      })
    },
    /**
     * 初始化菜单列表
     */
    initMenus() {
      const resp = require('./homeView.json');
      this.menus = resp;
      //默认展开
      this.defaultPage(resp);
    },
    /**
     * 默认展开资源
     * @param resp
     */
    defaultPage(resp) {
      function getPage(val) {
        if (val[0].children) return getPage(val[0].children);
        else return val[0];
      }

      let rs = getPage(resp);
      this.menuDefaultActive = rs.index;
      this.goPath(rs.url);
    },
    /**
     * 初始化系统信息
     */
    initVersionInfo() {

      this.systemInfo.name = '云上游乐园'
      this.systemInfo.version = '2.0'

    },
    goPath(path) {
      this.$router.push(path);
    },
    //滚动公告
    startMove() {
      // eslint-disable-next-line
      let timer = setInterval(() => {
        if (this.scroll.number === this.scroll.textArr.length - 1) {
          this.scroll.number = 0;
        } else {
          this.scroll.number += 1;
        }
      }, 5000); // 滚动不需要停顿则将2000改成动画持续时间
    },
  }
}
</script>
